<template>
  <v-container>
    <v-btn to="/home">Home</v-btn>
    <v-btn align-right to="/addlivestock">Add Livestock</v-btn>
    <form>
    <v-layout row wrap v-for="livestock in livestock" :key="livestock.livestockId" class="mb-3">
      <v-flex xs12 sm10 md8 offset-sm1 offset-md2>
        <v-card class="info" >
          <v-container fluid>
            <v-layout row>
              <v-flex xs7 sm8 md9>
                <v-card-title primary title>
                  <h4 class="white--text mb-0">{{ livestock.stockName }}</h4>
                    <v-spacer></v-spacer><div>
                  <div>Breed = {{ livestock.breed }}</div>
                  <div>Number in stock = {{ livestock.numInStock }}</div>
                  <div>Date added = {{ livestock.date }}</div>
                  <div>Additional livestock information = {{ livestock.addInfo }}</div>
                </div>
                <v-flex right>
                  <app-register-birth-dialog :livestock="livestock"></app-register-birth-dialog>
                  <app-edit-livestock-details-dialog :livestock="livestock"></app-edit-livestock-details-dialog>
                    </v-flex>
                </v-card-title>
                <v-card-actions>
                </v-card-actions>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card>
      </v-flex>
    </v-layout>
      <v-flex>

        </v-flex>
      </form>
  </v-container>
</template>

<script>
export default {
  computed: {
    livestock () {
      return this.$store.getters.loadedLivestock
    }
  }
}
</script>



